.text-overflow {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
.close-Icon {
  color: #e8e8e8;
  font-size: 12px;
  -webkit-transition: color 0.3s;
  transition: color 0.3s;
  cursor: pointer;
}
.close-Icon:hover {
  color: rgba(0, 0, 0, 0.335);
}
.RA-userInfo {
  color: var(--text-color);
  display: flex;
  font-size: 14px;
  height: 100%;
}
.RA-userInfo > span {
  font-weight: bold;
}
.RA-userInfo .RA-userInfo-menuItem {
  line-height: normal;
  line-height: initial;
  margin-left: 8px;
}
.RA-userInfo .RA-userInfo-icon {
  font-size: 18px;
  font-weight: bold;
  margin-right: 8px;
}
.RA-userInfo .RA-userInfo-dropdown {
  display: flex;
  align-items: center;
  cursor: pointer;
  padding: 0 8px;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  line-height: 60px;
}
.RA-userInfo .RA-userInfo-dropdown:hover {
  background: rgba(0, 0, 0, 0.025);
  color: var(--primary);
}
.RA-userInfo .RA-userInfo-userName {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
@media screen and (max-width: 600px) {
  .RA-userInfo .RA-userInfo-userName {
    width: 50px;
  }
}
